.hero {
  min-height: 100vh;
  width: 100%;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  text-align: center;
  animation: fadeIn 1.2s ease-in-out;
  padding: 20px; /* Prevents text hitting edges on mobile */
}

.hero h1 {
  font-size: 3rem;
  margin-bottom: 0.5rem;
}

/* Specific class for your PhD/Research line */
.subtitle {
  font-size: 1.3rem;
  opacity: 0.9;
  margin-bottom: 1.5rem;
}

/* Education Container */
.hero-education {
  margin-top: 1.5rem;
  padding-top: 1.5rem;
  border-top: 1px solid rgba(255, 255, 255, 0.3); /* Subtle divider */
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
}

.edu-line {
  font-size: 1.1rem;
  font-weight: 300;
}

.edu-line strong {
  font-weight: 600;
  color: #fff;
}

.edu-year {
  margin-left: 10px;
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.95rem;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(25px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Mobile Adjustments */
@media (max-width: 768px) {
  .hero h1 {
    font-size: 2.2rem;
  }
  .subtitle {
    font-size: 1.1rem;
  }
  .edu-line {
    font-size: 0.95rem;
    padding: 0 10px;
  }
  .edu-year {
    display: block; /* Stacks year under degree on small phones */
    margin-left: 0;
  }
}