body {
  margin: 0;
  padding: 0;
  font-family: "Inter", sans-serif;
  font-family: "Inter", system-ui, sans-serif;
  background: radial-gradient(circle at top, #020617, #000);
  color: #e5e7eb;
}

.navbar {
  display: flex;
  justify-content: space-between;
  padding: 20px 40px;
  position: sticky;
  top: 0;
  background: rgba(0,0,0,0.7);
  backdrop-filter: blur(10px);
}

.navbar a {
  margin-left: 20px;
  color: #38bdf8;
  font-weight: 500;
}

.hero {
  text-align: center;
  padding: 120px 20px;
}

.hero h1 {
  font-size: 3.5rem;
}

.button {
  display: inline-block;
  margin-top: 20px;
  padding: 12px 24px;
  border: 1px solid #38bdf8;
  color: #38bdf8;
  border-radius: 6px;
}

section {
  max-width: 1000px;
  margin: auto;
  padding: 80px 20px;
}

.card img {
  width: 100%;
  border-radius: 10px;
  margin-top: 15px;
}

/* ===== GLOBAL MOBILE FIXES ===== */
* {
  box-sizing: border-box;
}

html, body {
  width: 100%;
  overflow-x: hidden;
}

/* ===== HERO ===== */
@media (max-width: 768px) {
  .hero {
    padding: 70px 16px;
  }

  .hero h1 {
    font-size: 2.1rem;
    line-height: 1.2;
  }
}

/* ===== NAVBAR ===== */
@media (max-width: 768px) {
  .navbar {
    padding: 12px 16px;
  }

  .navbar a {
    margin-left: 0;
  }
}

/* ===== SECTIONS ===== */
@media (max-width: 768px) {
  section {
    padding: 50px 16px;
  }
}

/* ===== BUTTONS ===== */
@media (max-width: 768px) {
  .button {
    padding: 10px 18px;
    font-size: 0.95rem;
  }
}

